@import '../../styles/common';
@import '../../Form/styles/mixin';

// Full container.
.bttv-rs-form-fluid {
  .bttv-rs-form-control-wrapper {
    width: 100%;

    > .bttv-rs-input-number,
    > .bttv-rs-input {
      width: 100%;
    }
  }

  &.bttv-rs-form-vertical .bttv-rs-form-group .bttv-rs-input-group {
    width: 100%;
  }
}

// Fixed width.
.bttv-rs-form-control-wrapper {
  // Used to provide relation for error message
  position: relative;

  > .bttv-rs-input-number,
  > .bttv-rs-input {
    width: @form-control-width;
  }

  &.read-only {
    pointer-events: none;
    touch-action: none;
  }
}

// Vertical forms
.bttv-rs-form-vertical .bttv-rs-form-group .bttv-rs-input-group {
  width: @form-control-width;
}

.bttv-rs-form-vertical .bttv-rs-form-group .bttv-rs-form-control-wrapper {
  display: inline-block;
  // Make sure max width won't overflow.
  max-width: 100%;
}

// Horizontal forms
//
// Horizontal forms are built on grid classes and allow you to create forms with
// labels on the left and inputs on the right.

.bttv-rs-form-horizontal .bttv-rs-form-group {
  .bttv-rs-form-control-wrapper {
    float: left;
  }

  .bttv-rs-form-control-wrapper + .bttv-rs-form-help-text {
    clear: both;
  }

  // Help block
  .bttv-rs-form-control-wrapper + .bttv-rs-form-help-text:not(.bttv-rs-form-help-text-tooltip) {
    margin-left: @form-control-label-width + @form-control-label-margin-right;
  }
}

// Inline
.bttv-rs-form-inline .bttv-rs-form-group {
  .bttv-rs-form-control-wrapper {
    display: inline-block;
  }

  // Form control wrapper behind Screen only dom no need margin left.
  .bttv-rs-sr-only + .bttv-rs-form-control-wrapper {
    margin-left: 0;
  }
}
