@import '../../styles/common';
@import '../../styles/mixins/listbox';
@import '../../internals/Picker/styles/mixin';
@import 'mixin';

@custom-picker-tree-node-padding-vertical: @picker-tree-node-padding-vertical - 2px;
// Tree Picker
// ----------------------

.bttv-rs-tree {
  max-height: 360px;
  height: 100%;
  overflow-y: auto;
  flex: 1 1 auto;

  &.bttv-rs-tree-virtualized {
    overflow: hidden;
  }

  &-drag-preview {
    position: absolute;
    top: 0;
    color: var(--bttv-rs-text-primary);
    background-color: var(--bttv-rs-bg-overlay);
    display: inline-block;
    margin: 0;
    padding: @picker-tree-node-padding-vertical @picker-tree-node-padding-horizontal;
    border-radius: 6px;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.12);
    z-index: -1;
  }
}

.bttv-rs-tree-node {
  position: relative;
  // Clear host whitespace
  font-size: 0;
  text-align: left;
  margin: 0 0 4px 0;

  &-label {
    position: relative;
    margin: 0;
    //text gap
    padding: 1px 1px 1px (@picker-tree-arrow-down-width + 8px);
    display: inline-block;
    cursor: pointer;
    font-size: @picker-tree-node-font-size;
    line-height: @picker-tree-node-line-height;

    &:focus-visible {
      .bttv-rs-tree-node-label-content {
        .focus-ring();
      }
    }

    &-content {
      padding: 6px 8px;
      display: inline-block;
      border-radius: 6px;

      &:hover,
      &:focus,
      &&-focus {
        .listbox-option-active();
      }
    }
  }

  &-active &-label-content {
    .picker-item-active();

    color: var(--bttv-rs-text-link);
    background-color: var(--bttv-rs-listbox-option-selected-bg);
  }

  &-drag-over {
    background-color: var(--bttv-rs-listbox-option-hover-bg);
  }

  &-dragging {
    outline: 1px dashed var(--bttv-rs-primary-500);
  }

  &-drag-over-top {
    &.bttv-rs-tree-node-label-content {
      border-top: 2px solid var(--bttv-rs-text-link) !important;

      &::after {
        content: '';
        position: absolute;
        top: -2px;
        left: 12px;
        width: 0;
        height: 0;
        border-left: 6px solid var(--bttv-rs-text-link);
        border-top: 3px solid transparent;
        border-bottom: 3px solid transparent;
      }
    }
  }

  &-drag-over-bottom {
    &.bttv-rs-tree-node-label-content {
      border-bottom: 2px solid var(--bttv-rs-text-link) !important;

      &::after {
        content: '';
        position: absolute;
        bottom: -2px;
        left: 12px;
        width: 0;
        height: 0;
        border-left: 6px solid var(--bttv-rs-text-link);
        border-top: 3px solid transparent;
        border-bottom: 3px solid transparent;
      }
    }
  }

  &-drag-disabled {
    &.bttv-rs-tree-node-label-content {
      cursor: not-allowed;
    }
  }

  &-disabled {
    color: var(--bttv-rs-listbox-option-disabled-text);
    cursor: @cursor-disabled;

    > .bttv-rs-tree-node-label > .bttv-rs-tree-node-label-content {
      cursor: @cursor-disabled;
      color: var(--bttv-rs-listbox-option-disabled-text);

      &,
      &:hover {
        background-color: transparent;
      }
    }
  }

  &-disabled&-active > .bttv-rs-tree-node-label > .bttv-rs-tree-node-label-content {
    &,
    &:hover {
      color: var(--bttv-rs-listbox-option-disabled-selected-text);
    }
  }

  &:not(&-disabled):focus > .bttv-rs-tree-node-label > .bttv-rs-tree-node-label-content {
    background-color: var(--bttv-rs-listbox-option-selected-bg);
  }

  &:not(&-disabled) > .bttv-rs-tree-node-label:focus > .bttv-rs-tree-node-label-content {
    background-color: var(--bttv-rs-listbox-option-selected-bg);
  }
}

// expand icon
.bttv-rs-tree-node-expand-icon-wrapper {
  display: inline-block;
  position: absolute;
  cursor: pointer;
  z-index: 1;

  > .bttv-rs-tree-node-expand-icon {
    display: inline-block;
    padding: @picker-tree-node-padding-vertical 0;
    padding-right: @picker-tree-arrow-down-gap;
    height: 36px;
    font-size: 16px;
    line-height: @picker-tree-node-line-height;
    transform: rotate(-90deg) /* rtl: rotate(90deg) translateX(-13px) translateY(-15px) */;
    transform-origin: 1.5px 14px;
    margin-top: 6px;
    user-select: none;

    // expand children tree
    .bttv-rs-tree-open > .bttv-rs-tree-node & {
      transform: rotate(0deg);
      margin-left: 0;
      margin-top: 0;
    }
  }

  > .bttv-rs-tree-node-expanded {
    transform: rotate(0deg);
    margin-left: 0;
    margin-top: 0;
  }

  > .bttv-rs-tree-node-custom-icon,
  > .bttv-rs-tree-node-loading-icon {
    .node-item-prepend-icon();
  }
}

// node children
.bttv-rs-tree-node-children {
  > .bttv-rs-tree-group {
    position: relative;
    display: none;

    .bttv-rs-tree-open& {
      display: block;
    }
  }
}

.bttv-rs-tree-indent-line {
  height: 100%;
  width: 1px;
  position: absolute;
  top: 0;
  margin-left: -13px;
  border-left: 1px solid rgba(0, 0, 0, 0.1);
}

.bttv-rs-picker-popup {
  .bttv-rs-tree {
    padding-right: @picker-menu-padding;

    &.bttv-rs-tree-virtualized {
      padding: 0;
    }

    &-node {
      margin: 0;

      > .bttv-rs-tree-node-label > .bttv-rs-tree-node-label-content {
        .ellipsis();

        display: block;
        line-height: @line-height-computed;
      }
    }
  }
}

/* rtl:begin:ignore */

[dir='rtl'] .bttv-rs-tree-node-expand-icon-wrapper {
  right: 0;
  padding-right: inherit;
}

/* rtl:end:ignore */
