@import '../../styles/common.less';
@import '../../toaster/styles/animation.less';
@import '../../internals/CloseButton/styles/index.less';
@import './mixin.less';

//
// Messages
// --------------------------------------------------

// Base styles
// -------------------------

.bttv-rs-message {
  border-radius: @message-border-radius;
  font-size: @font-size-base;
  line-height: @line-height-base;
  position: relative;
  animation-fill-mode: forwards;
  overflow: hidden;

  &-container {
    padding: @message-padding;
    padding-right: @message-padding + 8px;
    display: flex;
  }

  &-centered {
    .bttv-rs-message-container {
      flex-direction: column;
      align-items: center;
      text-align: center;
      gap: 20px;
    }

    .bttv-rs-message-icon {
      align-self: center !important;

      .bttv-rs-icon {
        font-size: 40px !important;
      }
    }

    &.bttv-rs-message-bordered {
      border-left-width: 1px !important;
      border-top-width: 4px !important;
    }
  }

  // Message icon
  &-icon {
    align-self: center;
    font-size: 0; // remove whitespace before svg
    margin-right: 10px;

    // Icon
    .bttv-rs-icon {
      font-size: @message-icon-size;
      align-items: center;
    }
  }

  &-content {
    flex-grow: 1;
  }

  // Header
  &-header {
    color: var(--bttv-rs-text-heading);
    line-height: unit((round((@message-icon-large-size / @message-title-size), 16)));
    font-weight: bold;
    .ellipsis();

    & + .bttv-rs-message-body {
      margin-top: 4px;
    }
  }

  // Description
  &-body {
    color: var(--bttv-rs-text-primary);
    ul,
    ol {
      margin: 0;
      padding: 0;
      padding-inline-start: 16px;
    }
  }

  // Message with a title
  &.bttv-rs-message-has-title {
    // Bigger icon
    .bttv-rs-message-icon {
      align-self: flex-start;

      .bttv-rs-icon {
        font-size: @message-icon-large-size;
        line-height: 1;
      }
    }
  }

  // Multiple messages should have spacing
  // Hiding Message haven't spacing.
  &:not(.bttv-rs-message-hiding) + & {
    margin-top: 10px;

    //The last message hiding haven't clearance.
    &.bttv-rs-message-hiding:last-child {
      transition: margin-top 0.1s linear 0.3s;
      margin-top: 0;
    }
  }

  &.bttv-rs-message-hiding {
    // The same with notification
    animation: notificationMoveOut 0.3s ease-in forwards;
  }

  // In dark mode, links inside Message body shows underline by default
  .dark-mode({
    .bttv-rs-btn-link,
    a {
      color: inherit;
      text-decoration: underline;
    }
  });
}

// Full the containers
.bttv-rs-message-full {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  border-radius: 0;
  z-index: @zindex-message-full;
}

// Alternate styles
// -------------------------
// Generate contextual modifier classes for colorizing the message.

.bttv-rs-message-success {
  // Don't write arguments in single line when using var()
  // it breaks stylelint
  .message-variant(
    var(--bttv-rs-message-success-bg);
    var(--bttv-rs-message-success-header);
    var(--bttv-rs-message-success-text);
    var(--bttv-rs-message-success-icon);
    var(--bttv-rs-message-success-border);
    var(--bttv-rs-message-success-icon-border);
  );

  .high-contrast-mode({
    border: 1px solid var(--bttv-rs-message-success-border);
  });
}

.bttv-rs-message-info {
  .message-variant(
    var(--bttv-rs-message-info-bg);
    var(--bttv-rs-message-info-header);
    var(--bttv-rs-message-info-text);
    var(--bttv-rs-message-info-icon);
    var(--bttv-rs-message-info-border);
    var(--bttv-rs-message-info-icon-border);
  );

  .high-contrast-mode({
    border: 1px solid var(--bttv-rs-message-info-border);
  });
}

.bttv-rs-message-warning {
  .message-variant(
    var(--bttv-rs-message-warning-bg);
    var(--bttv-rs-message-warning-header);
    var(--bttv-rs-message-warning-text);
    var(--bttv-rs-message-warning-icon);
    var(--bttv-rs-message-warning-border);
    var(--bttv-rs-message-warning-icon-border);
  );

  .high-contrast-mode({
    border: 1px solid var(--bttv-rs-message-warning-border);
  });
}

.bttv-rs-message-error {
  .message-variant(
    var(--bttv-rs-message-error-bg);
    var(--bttv-rs-message-error-header);
    var(--bttv-rs-message-error-text);
    var(--bttv-rs-message-error-icon);
    var(--bttv-rs-message-error-border);
    var(--bttv-rs-message-error-icon-border);
  );

  .high-contrast-mode({
    border: 1px solid var(--bttv-rs-message-error-border);
  });
}
