@import '../../styles/common';
@import '../../Checkbox/styles/index';
@import '../../internals/Picker/styles/index';

// Check Picker
// ----------------------

// Menu Items
.bttv-rs-picker-check-menu-items {
  .picker-menu-items-reset();
  .picker-menu-group-common(picker);
  .picker-menu-group-title(picker);
  .picker-menu-group-closed(picker);

  .bttv-rs-check-item.bttv-rs-checkbox-checked .bttv-rs-checkbox-checker > label {
    .picker-item-active();
  }

  &.bttv-rs-picker-check-menu-grouped {
    .bttv-rs-check-item .bttv-rs-checkbox-checker > label {
      padding-left: 52px;
    }

    .bttv-rs-check-item .bttv-rs-checkbox-checker .bttv-rs-checkbox-control {
      left: 26px;
    }
  }
}
