@import '../../styles/common.less';
@import '../../Button/styles/index.less';
@import '../../Input/styles/index.less';
@import '../../InputGroup/styles/index.less';
@import '../../Calendar/styles/index.less';
@import '../../internals/Picker/styles/index.less';
@import '../../DatePicker/styles/index.less';
@import '../../Stack/styles/index.less';

.bttv-rs-picker-daterange {
  .bttv-rs-input-group-addon {
    color: var(--bttv-rs-text-secondary);

    .bttv-rs-btn-close {
      padding: 0;
    }
  }

  &.bttv-rs-picker-subtle {
    .bttv-rs-input-group {
      border-color: transparent;
    }
  }

  > .bttv-rs-input-group.bttv-rs-input-group-inside .bttv-rs-input {
    padding-right: 0;
  }
}

// Date range Picker
// ----------------------

.bttv-rs-picker-popup {
  &&-daterange {
    padding: 0;
  }

  .bttv-rs-calendar {
    display: inline-block;
    height: 274px;
    padding-bottom: 12px;

    &:first-child {
      border-right: 1px solid var(--bttv-rs-border-primary);
    }

    &-header {
      width: 100%;
      text-align: center;
    }

    &-header-month-toolbar {
      float: none;
    }
  }

  .bttv-rs-calendar-month-dropdown {
    z-index: @zindex-date-range-picker-calendar-dropdown;

    &-list {
      width: 185px;
    }
  }

  .bttv-rs-picker-daterange-panel-show-one-calendar .bttv-rs-picker-toolbar {
    max-width: @date-range-picker-calendar-default-width;

    &-ranges {
      width: 190px;
    }
  }

  // Calendar single
  .bttv-rs-picker-daterange-calendar-single {
    .bttv-rs-calendar {
      border: 0;
      display: block;
      margin: auto;
    }
  }
}

// Header
.bttv-rs-picker-daterange-header {
  padding: @date-range-picker-header-padding-vertical @picker-menu-padding;
  font-size: @date-range-picker-header-font-size;
  line-height: @date-range-picker-header-line-height;
  border-bottom: 1px solid var(--bttv-rs-border-primary);

  .bttv-rs-picker-header-date:focus {
    background-color: transparent;
  }

  &.bttv-rs-picker-tab-active-end,
  &.bttv-rs-picker-tab-active-start {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-around;

    &::after {
      content: ' ';
      position: absolute;
      width: 50%;
      bottom: -1px;
      border-bottom: 2px solid #3498ff;
      left: 0;
    }
  }

  &.bttv-rs-picker-tab-active-end {
    &::after {
      left: 50%;
    }
  }
}

// Calendar group
.bttv-rs-picker-daterange-calendar-group {
  height: 274px;
  // Make sure group wrapper can put 2 date-panels even screen width is not enough.
  min-width: 492px;
}

// Predefined Ranges
.bttv-rs-picker-daterange-predefined {
  height: 366px;
  border-right: 1px solid var(--bttv-rs-border-primary);
  padding: 4px 0;

  .bttv-rs-btn {
    display: block;
  }
}
